import axios from 'axios';

let util = {

};
util.title = function (title) {
  title = title || 'z';
  window.document.title = title;
};

const ajaxUrl = '/';

util.ajax = axios.create({
  baseURL: ajaxUrl,
  timeout: 30000
});

util.getBlogList = function () {
  return axios.get('http://host805124830.s661.pppf.com.cn/api/v1/post');
};

export default util;




