<template>
  <div class="hello">

    <mu-container>
      <mu-circular-progress v-if="showloading" class="demo-circular-progress" :size="36"></mu-circular-progress>
      <!--<mu-expansion-panel>-->
        <!--<div slot="header">Panel 1</div>-->
        <!--Lorem ipsum dolor sit amet, consectetur adipiscing elit. Suspendisse malesuada lacus ex, sit amet blandit leo lobortis eget.-->
        <!--<mu-button slot="action" flat>Cancel</mu-button>-->
        <!--<mu-button slot="action" flat color="primary">Save</mu-button>-->
      <!--</mu-expansion-panel>-->
      <mu-expansion-panel v-for="(blog,index) in blogList">
        <div slot="header">{{blog.title}}</div>
        <div v-html="blog.content_html" style="text-align: left"></div>
      </mu-expansion-panel>

    </mu-container>

      <!--<p v-for="(blog,index) in blogList" @click="openDetail(blog.id)">{{blog.title}}<br/><div v-show="blog.id==showid" v-html="blog.content_html"></div><br/></p>-->

    <!--<button @click="getList">getList</button>-->
    <!--<div v-html="content"></div>-->
  </div>
</template>

<script>
  import util from '../common'
export default {
  name: 'home',
  data () {
    return {
      msg: 'home',
      blogList:[],
      content:null,
      showid:null,
      showloading:true
    }
  },
  created(){
    this.getList();
  },
  methods:{
    getList(){
      let _this = this;
      util.getBlogList()
        .then(data =>{
        //console.log(data);
          _this.showloading = false;
          if(data.data.status_code == 200){
            _this.blogList = data.data.data.reverse();
          }

      })
        .catch(err =>{
          console.log(err)
        });
    },
    openDetail(id){
      let _this = this;
      _this.showid = id == _this.showid ? null : id;
      //_this.content = _this.blogList[id].content_html;
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



